@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rock+Salt&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#avatarsTitle {
    font-family: 'Architects Daughter', cursive;
}

.avatarsContainer {
    border-radius: 5px;
    margin: 3px;
}

.avatar {
    cursor: pointer;
    width: 90px;
    border-radius: 45px;
}
#home-background {
    background-image: url(/static/media/lets_scribble_bg.1d35f0fe.png);
    width: 100vw;
    min-height: 100vh;
    height: 100%;
    background-size: 200px 200px;
    box-shadow: inset 0 0 0 2000px rgba(0, 0, 0, 0.5);
}

#title {
    color: whitesmoke;
    font-family: 'Rock Salt', cursive;
    font-size: 60px;
    margin: 0 0 30px 0;
    padding-top: 15px;
}

#card-container {
    display: flex;
    justify-content: center;
    padding-bottom: 50px;
}

#main-card {
    background-color: #f5f5f5;
    box-shadow: 0 0px 20px 0px rgba(0, 0, 0, 0.6);
    padding: 25px 0 12px 0;
    width: 720px;
}

#name-input-container {
    margin-bottom: 20px;
}

#buttons-container {
    margin-top: 30px;
}

/* #create-room-button {
    margin-top: 30px;
} */

@media screen and (max-width: 960px) {
    #or {
        margin: 20px 0 20px 0;
    }
}

@media screen and (max-width: 750px) {
    #title {
        font-size: 50px;
    }
    #main-card {
        width: 93%;
    }
}

@media screen and (max-width: 550px) {
    #title {
        font-size: 35px;
    }
}

@media screen and (max-width: 400px) {
    #title {
        font-size: 27px;
    }
}
#settingsFormContainer {
    width: 85%;
    margin-bottom: 25px
}

#settingsTitle {
    font-family: 'Rock Salt', cursive;
    margin-bottom: 0;
}

.inputs {
    width: 250px;
    margin: 20px
}

.fieldsContainer {
    padding: 20px 0 20px 0;
}

@media screen and (max-width: 960px) {
    .inputs{
        width: 95%;
    }
}
.memberName {
    margin: 4px 0 9px 0;
    word-break: break-all;
}

.lobbyAvatars {
    width: 90px;
    border-radius: 45px;
}
#modalContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

#avatars {
    text-align: center;
    width: 700px;
}

#submit {
    background-color: green;
    color: white;
    margin-top: 15px;
}

@media screen and (max-width: 750px) {
    #avatars {
        width: 85%;
    }
}
#lobbyTitle {
    color: purple;
    font-family: 'Rock Salt', cursive;
    font-size: 35px;
    margin: 0 0 25px 0;
    padding-top: 5px;
}

#roomID {
    font-size: 18px;
    margin: 5px 0 25px 0;
}

#lobbyBackground {
    background-color: thistle;
    height: 100%;
    min-height: 100vh;
}
#gameOverTitle {
    font-family: 'Rock Salt', cursive;
    color: white;
    margin: -5px 0 12px 0;
}

#resultsContainer {
    overflow-y: auto;
    max-height: 350px;
    margin-bottom: 25px;
}

table {
    width: 300px;
}

td {
    padding: 10px;
}

.resultContent {
    color: white;
    font-size: 20px;
}

@media screen and (max-width: 350px) {
    table {
        width: 250px;
    }
}
canvas {
    box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.8);
    z-index: 1;
}

.sketchboardLayers {
    position: absolute;
    left: 50%;
    transform: translateX(-50%)
}

#overlay {
    background-color: rgba(25,25,25,0.8);
    z-index: 2
}

#overlayContainer {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.overlayText {
    color: white;
    font-size: 25px;
}

#chooseWordTitle {
    font-family: 'Rock Salt', cursive;
}
#paletteContainer {
    display: flex;
    justify-content: center;
    margin-top: 10px;
}
#colorsContainer {
    display: flex;
}
.color {
    cursor: pointer;
    width: 40px;
    height: 40px;
    margin: 2px;
}
#scoreCardContainer {
    display: flex;
    justify-content: center;
}

#scoreCard {
    margin-bottom: 18px;
    width: 95%;
}

#scoreCardContent {
    overflow-y: auto;
    height: 85%;
}

.scoreCardBigScreen {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

.scoreCardSmallScreen {
    min-width: 80px;
}

.scoreCardAvatar {
    width: 46px;
    height: 46px;
    border-radius: 23px;
}

.scoreCardName {
    margin: 0 0 4px;
    text-align: right;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

.score {
    margin: 4px 0 0;
    text-align: right;
}

 @media screen and (max-width: 960px) {
    #scoreCardContent {
        display: flex;
        flex-direction: row;
        height: 90px;
        overflow-x: auto;
        margin: 0;
    }

    #scoreCard {
        width: 85%;
    }

    .scoreCardName {
        text-align: center;
    }

    .score {
        text-align: center;
        margin: 3px 0 0;
    }    
}
#guessInputForm{
    text-align: center;
}

#submitGuess {
    margin-top: 10px;
}
#chatboxContainer {
    height: 510px;
}

#chatbox {
    background-color: #f5f5f5;
    overflow-y: auto;
    display: block;
    text-align: left;
}

.guessContainer {
    margin: 0 10px 0 10px;
    word-break: break-all;
}

@media screen and (max-width: 960px) {
    #chatboxContainer {
        height: 100vh;
    }
}
#score {
    margin: 0;
    font-size: 20px;
    padding-right: 6px;
}
#gameBar {
    height: 50px;
    background-color: #f5f5f5;
}

#objectsContainer {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    height: 95%;
}
#playgroundBackground {
    background-image: url(/static/media/lets_scribble_bg.1d35f0fe.png);
    background-size: 200px 200px;
    min-height: 100vh;
    height: 100%;
    box-shadow: inset 0 0 0 2000px rgba(0, 0, 0, 0.6);
}

#playgroundTitle {
    color: white;
    font-family: 'Rock Salt', cursive;
    margin-top: 0;
}

#gameBarContainer {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}

#drawerButton {
    position: absolute;
    right: 20px;
    top: 20px;
}

#drawerChatbox {
    width: 250px;
    height: 100vh;
}

@media screen and (max-width: 500px) {
    #drawerButton {
        right: 0;
        top: 16px;
        font-size: 12.5px;
    }

    #playgroundTitle {
        font-size: 27px;
    }
}
.App {
  text-align: center;
}

::-webkit-scrollbar {
  width: 0.45em;
  height: 0.45em;
}

::-webkit-scrollbar-thumb {
  background: slategray;
}

::-webkit-scrollbar-track {
  background: #b8c0c8;
}
